.anim-circle {
  /* Add the blur effect */
  filter: blur(40px);
  -webkit-filter: blur(40px);

  /* overflow: hidden; */
  color: transparent;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 60vh;
  height: 60vh;
  border-radius: 100%;
  background: var(--animCircle);
  -webkit-animation: breathe 10s linear infinite;
          animation: breathe 10s linear infinite;
}

@-webkit-keyframes breathe {
  0% {
    box-shadow: 0 0 0 15px var(--animCircleLight),
      0 0 0 30px var(--animCircleLight), 0 0 0 45px var(--animCircleLight),
      0 0 0 60px var(--animCircleLight), 0 0 0 75px var(--animCircleLight),
      0 0 0 80px var(--animCircleLight), 0 0 0 85px var(--animCircleLight);
  }
  50% {
